import tokenRequest from '@/utils/tokenRequest'

// 获取地址列表
export function getAdressList () {
  return tokenRequest.get('/address/list')
}

// 获取默认的收获地址
export function getDefaultAdress () {
  return tokenRequest.get('/address/defaultId')
}

// 获取省份城市树
export function getCityTree () {
  return tokenRequest.get('/region/tree')
}

// 添加收获地址
export function addGoodsAdress (obj) {
  return tokenRequest.post('/address/add', obj)
}

// 设置默认地址
export function setDefaultAdress (id) {
  return tokenRequest.post('/address/setDefault', {
    addressId: id
  })
}

// 删除地址
export function removeAdress (id) {
  return tokenRequest.post('/address/remove', {
    addressId: id
  })
}

// 获取详情地址
export function getAdressDetial (id) {
  return tokenRequest.get('/address/detail', {
    params: {
      addressId: id
    }
  })
}

// 编辑收获地址
export function editAdress (obj) {
  return tokenRequest.post('/address/edit', obj)
}
