<template>
  <div class="box">
    <!-- 头部 -->
    <header>
      <NavBar>
        <template #title>新建收货人</template>
      </NavBar>
    </header>
    <!-- 主体 -->
    <main>
      <form>
        <table>
          <tr>
            <td>收货人</td>
            <td><input type="search" placeholder="请填写收货人姓名" v-model="formData.form.name"></td>
            <td><van-icon name="friends-o" /> 通讯录</td>
          </tr>
          <tr>
            <td>手机号码</td>
            <td>
              <select name="" id="" >
                <option value="+86">+86</option>
              </select>
            </td>
            <td><input type="search" inputmode="numeric" placeholder="请填写收货人手机号" v-model="formData.form.phone"></td>
          </tr>
          <tr>
            <td>所在地区</td>
            <td>
              <select class="ellipsisOneRow" v-model="select1">
                <option v-for="(item) in ProvinceList" :key="item.id" :value="item.id">{{item.name}}</option>
              </select>
              <select class="ellipsisOneRow" v-model="select2">
                <option v-for="(item) in CityList" :key="item.id" :value="item.id">{{item.name}}</option>
              </select>
              <select class="ellipsisOneRow" v-model="select3">
                <option v-for="(item) in OUList" :key="item.id" :value="item.id">{{item.name}}</option>

              </select>
            </td>
          </tr>
          <tr>
            <td>详细地址</td>
            <td><input type="search" placeholder="请填写详细地址"  v-model="formData.form.detail"></td>
            <td><van-icon name="service-o" /></td>
          </tr>
        </table>
      </form>
    </main>
    <!-- 尾部 -->
    <footer>
      <btnPrecent @click="addGoodsAdressAction">保存</btnPrecent>
    </footer>
  </div>
</template>

<script>
import btnPrecent from '@/components/button.vue'
import { getCityTree, addGoodsAdress, getAdressDetial, editAdress } from '@/api/adress'
import { Toast } from 'vant'
export default {
  name: 'editAndAdd',
  data () {
    return {
      formData: {
        form: {
          name: '',
          phone: '',
          detail: ''
        }
      },
      ProvinceList: [],
      CityList: [],
      OUList: [],
      select1: 1,
      select2: 2,
      select3: 3
    }
  },
  components: {
    btnPrecent
  },
  methods: {
    async getCityTreeAciton () {
      const { data: { list } } = await getCityTree()
      // 对象的结构转成数组
      // 一次性全部渲染，然后再监测改
      this.ProvinceList = Object.values(list)
      this.CityList = Object.values(this.ProvinceList[0].city)
      this.OUList = Object.values(this.CityList[0].region)
      // 等所有的城市列表拿到之后判定要不要回显
      // 如果有id的话就就回显
      if (this.isId) this.getAdressDetialAction()
    },
    async addGoodsAdressAction (e) {
      if (!/^\S+$/.test(this.dist.name)) {
        Toast('用户名不能为空且不包含空格')
        return
      } else if (!/^1[3-9]\d{9}$/.test(this.dist.phone)) {
        Toast('手机号码不合规')
        return
      } else if (!/^[\d\D]+$/.test(this.dist.detail)) {
        Toast('请填写详细地址')
        return
      }
      // 存放城市信息
      const region = []
      // 映射各级城市
      const arr1 = [this.ProvinceList, this.CityList, this.OUList]
      // 映射各级城市的值
      const arr2 = [this.select1, this.select2, this.select3]
      for (let i = 0; i < 3; i++) {
        // 根据城市值与城市查找出来城市名字
        region[i] = { value: arr2[i], label: arr1[i].find(item => item.id === arr2[i]).name }
      }
      this.formData.form.region = region
      if (this.isId) {
        this.formData.addressId = this.isId
        await editAdress(this.formData)
        Toast('修改成功')
      } else {
        await addGoodsAdress(this.formData)
        Toast('添加成功')
      }
      this.$router.replace('/adresslist')
    },
    // 有id就请求并回显
    async getAdressDetialAction () {
      console.log(this.isId)
      const { data: { detail } } = await getAdressDetial(this.isId)
      const obj = this.formData.form
      const { city_id: cityID, province_id: provinceId, region_id: regionID } = detail
      obj.name = detail.name
      obj.phone = detail.phone
      obj.detail = detail.detail
      this.select1 = provinceId
      this.select2 = cityID
      this.select3 = regionID
    }
  },
  computed: {
    dist () {
      return this.formData.form
    },
    // 查询参数
    isId () {
      return this.$route.query.id
    }
  },
  watch: {
    // 监测第一个变化，来渲染第二个
    select1 (n) {
      this.CityList = Object.values(this.ProvinceList.find(item => item.id === n).city)
      this.select2 = this.CityList[0].id
    },
    // 检测第二个变化，来渲染第三个
    select2 (n) {
      this.OUList = Object.values(this.CityList.find(item => item.id === n).region)
      this.select3 = this.OUList[0].id
    }
  },
  created () {
    this.getCityTreeAciton()
  }
}
</script>

<style lang="less" scoped>
main {
  position: fixed;
  top: 50px;
  left: 0;
  right: 0;
  bottom: 60px;
  padding:15px 15px 0;
  form {
    border-radius: 10px;
    background: #fff;
    padding:0 15px;
    table {
      width: 100%;
      font-size: 12px;
      tr {
        display: flex;
        align-items: center;
        height: 50px;
         td {
          input {
            outline: none;
            border: 0;
            width: 170px;
          }
          select {
            border: 0;
            font-weight: bold;
          }
            display: inline-block;
            &:nth-child(1) {
              font-weight: 600;
              width: 25%;
            }
            &:nth-child(2) {
              width: 50%;
            }
            &:nth-child(3) {
              text-align: right;
              width: 25%;
            }
        }
        &:nth-child(1) {
          td {
            &:nth-child(3) {
              color: gray;
             i {
              font-size: 18px;
              transform: translate(0, 2px);
             }
            }
          }
        }
        &:nth-child(2) {
          td {
            &:nth-child(2) {
              width: 50px;
              select {
                font-size:12px ;
              }
            }
          }
        }
        &:nth-child(3) {
          td {
            &:nth-child(2) {
              width: 75%;
              select {
                width: 71px;
                margin-right: 5px;
              }
            }
          }
        }
        &:nth-child(4) {
          td {
            input {
              width: 200px;
            }
            &:nth-child(3) {
              font-size: 16px;
              color: gray;
            }
          }
        }
      }
    }
  }
}
footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
  padding: 10px 20px;
}
</style>
